<template>
  <div class="wrap">
    <listSearch v-model="searchData" :searchArr="searchArr" @searchConfirm="goSearch"></listSearch>
    <div class="table_box">
      <div class="audit_button">
        <!-- <Button type="primary" @click="handleSubmit">上传模版</Button> -->
        <Button type="primary" @click="addDraft">新建草稿</Button>
      </div>
      <Table :columns="columns" :data="tableData">
      <template slot-scope="{ row}" slot="action">
        <div>
          <button @click="btnOperate(1, row)">查看</button>
          <button @click="btnOperate(2, row)">删除</button>
          <button @click="btnOperate(3, row)">编辑</button>
          <button @click="btnOperate(4, row)">提交</button>
        </div>
      </template>
      </Table>
      <Page
        style="margin-top:20px"
        :current="pageObj.pageIndex"
        :total="pageObj.total"
        :page-size="pageObj.pageSize"
        :page-size-opts="[10,20,50]"
        show-sizer
        show-elevator
        show-total
      />
    </div>
    <templateEditor :alertShow="editorShow"
      :alertName="alertShow.editorName"
      :alertCode="alertShow.editorCode"
      :alertToken="alertShow.editorToken"
      :alertTypeId="alertShow.editorTypeId"
      :alertContent="alertShow.editorContent"
      :alertDocument="alertShow.documentType"
      :alertDisType="alertShow.disType"
      @alertConfirm="editorSave"
      @alertCancel="alertCanc('editor')"
      alertTitle="编辑"></templateEditor>
  </div>
</template>
<script>
import {mapActions} from 'vuex'
import listSearch from "@/components/list-search/search.vue";
import templateEditor from './template'
// const columns =[{title:'模板名称'}, {title:"模板文书类型"},{title:'注册人名称'},{title:'案件类型名称'},{title:"创建时间"},{title:"保存时间"},{title:"审核状态"}
// ,{title:"操作",width:300,slot:"action"}]
export default {
  components: {
    listSearch,
    templateEditor
  },
  data() {
    return {
      pageObj: {
        pageIndex: 1,
        pageSize: 10,
        total: 0,
      },
      searchData: {
        tempName: "",
        regName:"",
        tempType:[]
      },
      operateObj: {
        2: {type: 1, content: '确定删除该模版', func: this.deleteTemplate},
        4: {type: 4, content: '确定提交模版审核', func: this.submitTemplate}
      },
      editorShow: false,
      alertShow: {
        disType: false,
        find: false,
        editor: false,
        editorDest: false,
        editorName: null,
        editorCode: null,
        editorToken: null,
        editorTypeId: null,
        editorContent: null,
        editorTempId: null,
        documentType: null,
        reas: false,
        reasText: '',
        subm: false,
        sumbId: null
      },
      searchArr: [
        {
          type: "input",
          value: "tempName",
          label: "模板名称",
          placeholder: "请输入模板名称"
        },
        {
          type: "input",
          value: "regName",
          label: "注册名称",
          placeholder: "请输入注册名称"
        },
        {
          type: "select",
          value: "tempType",
          label: "模板类型",
          placeholder: "请选择模板类型",
          arr: [
            { label: "全部", value: 0 },
            { label: "通过", value: 1 },
            { label: "未通过", value: 2 }
          ],
        }
      ],
      columns: [
        {title:'模板名称', key: 'tempName'},
        {title:"模板文书类型", key: 'caseType'},
        {title:'注册人名称', key: 'registerName'},
        {title:'案件类型名称', key: 'caseType'},
        {title:"创建时间", key: 'createTime'},
        {title:"保存时间", key: 'updateTime'},
        {title:"驳回原因", key: 'updateTime'},
        {title:"审核状态", key: 'status'},
        {title:"操作",width:300,slot:"action"}
      ],
      tableData:[{}]
    };
  },
  mounted () {
    this.getList()
  },
  methods: {
    ...mapActions({
      getTemplateList: 'organizePeriod/getTemplateList',
    }),
    goSearch() {},
    // 按钮操作 1:查看 2:删除 3：编辑 4:提交
    btnOperate(type, data) {
      this.confirmOperate(type)
    },
    confirmOperate (type) {
      this.$Modal.confirm({
          title: '操作',
          content: '<div style="color:#3B4453;text-align:center;font-size:18px">'+this['operateObj'][type]['content']+'？</div>',
          onOk: () => {
              this['operateObj'][type]['func']()
          },
          onCancel: () => {
              
          }
      });
    },
    handleLook(row){
        
    },
    handleOver(row){
      this.$Modal.confirm({
          title: '操作',
          content: '<div style="color:#3B4453;text-align:center;font-size:18px">确定审核通过？</div>',
          onOk: () => {
              this.$Message.success('操作成功');
              this.$Message.error('系统出错，请稍后重试')
          },
          onCancel: () => {
              
          }
      });
    },
    backCase(row){
        this.$Modal.confirm({
            title: '操作',
            content: '<div style="color:#3B4453;text-align:center;font-size:18px">确定审核驳回操作？</div>',
            onOk: () => {
                this.$Message.success('操作成功');
                this.$Message.error('系统出错，请稍后重试')
            },
            onCancel: () => {
               
            }
        });
    },
    // 删除模版
    deleteTemplate () {
      console.log(11)
    },
    // 提交审核
    submitTemplate () {
      console.log(22)
    },
    // 列表
    getList () {
      let params = {}
      Object.assign(params, this.pageObj)
      params.menuIdentifer = "MANAGE_TEMPLATE_LIST"
      this.getTemplateList (params)
        .then(res => {
          console.log(res)
          if (res.data.flag) {
            this.tableData = res.data.data.data
          } else {
            this.$Message.error(res.data.message)
          }
        })
        .catch(err=> {
          console.log(err);
        })
    },
    // 添加草稿
    addDraft () {
      this.editorShow = true
    },
    // 保存编辑
    editorSave () {

    },
    // 弹窗取消
    alertCanc () {
      this.editorShow = false
    }
  }
};
</script>
<style lang="less" scoped>
/deep/.ivu-table th {
  background-color: #3296fa;
}
.wrap {
  .table_box {
    margin-top: 10px;
    background: #fff;
  }
  .audit_button {
    padding: 10px 0;
    display: flex;
    justify-content: flex-end;
    padding-right: 20px;
    // margin-bottom: 20px;
    button:first-child {
      margin-right: 10px;
    }
  }
}
</style>
